.overlay {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 100;
  background: rgba(0, 0, 0, 0.6);
  pointer-events: none;
  opacity: 0;
  transition: opacity 0.4s;

  &.show {
    pointer-events: all;
    opacity: 1;
  }
  .overlayInner {
    width: 80%;
    padding: 0.8rem;
    border: 0.6rem solid #29a46a;
    border-radius: 0.6rem;
    color: #333;
    background-color: #fff;
    box-shadow: 0px 5px 5px 0px rgba(0,0,0,0.45);

    .btn {
      margin-right: 0.6rem;
    }
  }

  &.intermission {
    .overlayInner {
      background-image: url('/voltorb.svg');
      background-repeat: no-repeat;
      background-position: right;
      background-size: 50%;
    }
  }
}